.i{
    display: flex;
    height: 100vh;

}

.i-left{
  flex:1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.i-left-wrapper{
  padding: 50px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  
  
}
.i-desc{
  font-size: 20px;
}

.i-intro{
  font-size: 30px;
  font-weight: 300;
  font-stretch: expanded;
  animation: text 20s infinite ;

}
.i-name{
   font-size: 80px;
   animation: text 7s infinite;
}

@keyframes text {
  0%{
    color: orange;
    
    
  }
   25%{
     color:brown;
   
   
  }
  50%{
        color:yellowgreen;
    
    }
  75%{
       color:red;
     
  }
  100%{
        color:goldenrod;
       
  }
  
}
.i-title{
  height: 50px;
  overflow: hidden;
   
}
.i-title-wrapper{
  height: 100%;

  animation: move 10s ease-in-out infinite alternate;
}
@keyframes move {
  25%{
    transform: translateY(-50px);
  }
  50%{
    transform: translateY(-100px);
  }
  75%{
    transform: translateY(-150px);
  }
  100%{
    transform: translateY(-200px);
  }
}
.i-title-item{
  height: 50px;
  font-size: 30px;
  font-weight: bold;

  
  
  display: flex ;
  align-items: center;
}
.i-desc-wrapper{
  height: 10%;
  animation: move 20s ease-in-out infinite alternate;
}
.i-desc-item{
  height: 40px;
  font-size: 30px;
  font-weight: bold;
  color: rgb(255, 187, 0);
  
  display: flex ;
  align-items: center;
}
.i-desc{
