  #box-img{
   height: 100%;
   width:100%;
   object-fit: fill;
  
  }
  
  .container .box {
      position: absolute;
       
      width: calc(400px - 60px);
      height: calc(400px - 30px);
      background: rgb(248, 248, 248);
      display: inline-block;
      margin: 0px 80px 60px 2px;
      box-sizing: border-box;
      overflow: hidden;
     
      border-radius: 50px;
      box-shadow: 20px 20px 50px rgba(232, 0, 0, 0.5);
  }
  
  .container .box .text {
      position: absolute;
      top: 0;
      left: 0%;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.473);
      box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
      border-top: 1px solid rgba(255, 255, 255, 0.5);
      border-top: 1px solid rgba(255, 254, 254, 0.5);
      transition: 0.6s;
      z-index: 1;
  }
  
  .container .box:hover .text {
      top: 10px;
      left: calc(72% - 40%);
      width: 100px;
      height: 100px;
      border-radius: 50%;
      overflow: hidden;
  }
  
  .container .box .text .fa {
      position: absolute;
      top: 50%;
      left: 50%;
      right: 50%;
      transform: translate(-50%, -50%);
      font-size: 50px;
      transition: 0.5s;
      width: 100%;
  }
  
  .fa {
      color: white;
      display: flex;
      justify-content: center;
      
  }
  
